___CSS_LOADER_EXPORT___.push([module.id, `*,
*::before,
*::after {
  background-color: white;
  box-sizing: border-box;
}

body,
.dropdown-menu-btn {
  --site-width: 450px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.menu {
  display: grid;
  grid-template-columns: 1fr 40px;
  align-items: stretch;

  width: var(--site-width);
  height: 40px;
}

.border {
  border: 2px solid black;
  border-radius: 4px;
}

.menu > .title {
  align-self: center;
  margin-left: 20px;
}

.dropdown-container {
  position: relative;
}

.dropdown-menu-btn {
  border: none;
  height: 100%;
}

.dropdown-menu {
  display: none;
  width: 75px;
  position: absolute;
  right: -2px;
}

.dropdown-menu.visible {
  --dropdown-item-size: 4px;

  display: flex;
  flex-direction: column;
  z-index: 5;
}

.dropdown-menu > button {
  width: 100%;
  height: 36px;
  border: none;
}

.carousel {
  --image-width: 380px;
  display: grid;
  grid-template-columns: 1fr var(--image-width) 1fr;
  grid-template-rows: 250px 40px;
  width: var(--site-width);
  margin-top: 8px;
}

.carousel > * {
  border: solid 1px black;
}

.left,
.right {
  grid-row: 1 / span 2;
  padding: 0;
}

.frame {
  grid-row: 1;
  position: relative;
  overflow: hidden;
}

.images {
  position: absolute;
  display: flex;
  height: 100%;
  background-color: paleturquoise;
}

.images > img {
  width: var(--image-width);
}

.nav-btns {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.nav-btns > button {
  width: 12px;
  height: 12px;
  border: 2px solid black;
  border-radius: 50%;
  padding: 0;
}

.nav-btns > button.active {
  background-color: black;
}

form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;

  width: var(--site-width);
  margin-top: 8px;
  padding: 16px 32px;
}

form > legend {
  grid-column: span 2;
  text-align: center;
  font-weight: bold;
  font-size: 1.4rem;
  padding-bottom: 16px;
}

form > button {
  grid-column: span 2;
  border-radius: 8px;
  margin: 10px 0;
  padding: 8px;
}

form * {
  width: 100%;
  font-size: 1.1rem;
  border-radius: 4px 4px 0 0;
}

output {
  grid-column: span 2;
  text-align: center;
}

form .dirty:invalid {
  border: 2px solid #900;
}

.error {
  padding: 0;
  font-size: 80%;
  color: white;
  background-color: #900;
  border-radius: 0 0 5px 5px;
}

.error.active {
  padding: 0.3em;
}

.gif {
  display: grid;
  grid-auto-rows: 25px 25px 1fr;
  gap: 12px;
  justify-content: center;

  height: 400px;
  width: var(--site-width);
  margin-top: 20px;
  padding: 16px 16px;
}

.gif > div {
  margin: auto;
}

.gif > button {
  width: 150px;
  justify-self: center;
}

.gif > img {
  justify-self: center;
  align-self: center;
  border: 2px solid black;
  max-width: 400px;
  max-height: 280px;
  object-fit: contain;
}
